import React, { useState, useRef } from 'react';
import styled from 'styled-components';
import { Hamburger } from '../HumberButton';
import {
  animated,
  useTransition,
  useSpring,
  useChain,
  config
} from 'react-spring';
import { Link } from 'gatsby';
// import 'normalize-css';

import { VerticalLink } from '../link/verticalLink';

const Button = styled(Hamburger)`
  position: fixed;
  top: 30px;
  right: 30px;
  font-size: inherit;
`;
const Nav = styled(animated.nav)`
  position: fixed;
  background: var(--primary);
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow-y: auto;
  transform-origin: 100% 50%;
  right: 0px;
  width: 100%;
  left: auto;

  ul {
    margin: 0;
    padding: 0;
  }
`;

const Li = styled(animated.li)`
  list-style: none;
  color: #fff;

  :before {
    content: '';
    left: -25px;
    width: 10px;
    height: 70px;
    position: absolute;
    background: linear-gradient(to right, #f09819, #ff512f);

    z-index: -1;
    transition: all 0.3s ease-in-out 0s;
  }

  :hover:before {
    visibility: visible;
    width: 100%;
  }
`;

const MenuLink = styled(Link)`
  color: #fff;
  font-size: 50px;
  font-weight: bold;
  text-transform: uppercase;
  text-decoration: none;
  line-height: 1.3;

  :hover:before {
    visibility: visible;
    transform: scaleX(1);
  }

  :hover {
    text-decoration: none;
    color: #fff;
  }
`;

const menuItems = [
  { name: 'Home', link: '/' },
  { name: 'Notes', link: '/notes/' },
  { name: 'Portfolio', link: '/portfolios/' }
];

const Container = styled.div`
  position: relative;
  z-index: 1400;
`;

export const SideNav = ({ className }) => {
  const [showMenu, setShowMenu] = useState(false);

  React.useEffect(() => {
    if (showMenu) document.body.style.overflow = 'hidden';
    else document.body.removeAttribute('style');
  }, [showMenu]);

  const toggleMenu = () => {
    setShowMenu(!showMenu);
  };

  // Set refs - required for useChain
  const navRef = useRef();
  const liRef = useRef();

  // Setup animation for nav element
  const springProps = useSpring({
    ref: navRef,
    config: config.default,
    from: { transform: 'scaleX(0)' },
    to: {
      transform: showMenu ? 'scaleX(1)' : 'scaleX(0)'
    }
  });

  // Setup animations for nav items
  const liTransitions = useTransition(
    showMenu ? menuItems : [],
    (item) => item.name,
    {
      ref: liRef,
      trail: 400 / menuItems.length,
      from: { opacity: 0, transform: 'translateY(20px)' },
      enter: { opacity: 1, transform: 'translateY(0)' },
      leave: { opacity: 0, transform: 'translateY(20px)' }
    }
  );

  // On showMenu, start with nav animation then nav items
  useChain(showMenu ? [navRef, liRef] : [liRef, navRef], [
    0,
    showMenu ? 0.4 : 0.6
  ]);

  return (
    <Container className={className}>
      <Nav style={springProps}>
        <ul>
          {liTransitions.map(({ item, key, props }) => (
            <Li key={key} style={props}>
              <MenuLink to={item.link}>{item.name}</MenuLink>
            </Li>
          ))}
        </ul>
      </Nav>
      <Button open={showMenu} onClick={toggleMenu}>
        Toggle Menu
      </Button>
    </Container>
  );
};
