import React, { useState } from 'react';
import HeaderStyle from './Header.style';
import { useDidMount, useWindowEvent } from '../../hooks';

import styled from 'styled-components';

const Menu = ({ className }) => (
  <HeaderStyle.HeaderMenu className={className}>
    <HeaderStyle.MenuItem className="nav-item">
      <HeaderStyle.StyledLink to="/" activeClassName="selected">
        Home
      </HeaderStyle.StyledLink>
    </HeaderStyle.MenuItem>
    <HeaderStyle.MenuItem>
      <HeaderStyle.StyledLink
        to="/notes/"
        activeClassName="selected"
        state={{
          pleasant: 'reasonably'
        }}
      >
        Notes
      </HeaderStyle.StyledLink>
    </HeaderStyle.MenuItem>
    <HeaderStyle.MenuItem>
      <HeaderStyle.StyledLink
        to="/portfolios/"
        activeClassName="selected"
        state={{
          pleasant: 'reasonably'
        }}
      >
        Portfolios
      </HeaderStyle.StyledLink>
    </HeaderStyle.MenuItem>
    <HeaderStyle.MenuItem>
      <HeaderStyle.SocialLink href="https://github.com/MagicMike90">
        <HeaderStyle.StyledGithubIcon size={28} />
      </HeaderStyle.SocialLink>
    </HeaderStyle.MenuItem>
    <HeaderStyle.MenuItem>
      <HeaderStyle.SocialLink href="https://www.linkedin.com/in/michael-luo-10066110a/">
        <HeaderStyle.StyledLinkedinInIcon size={28} />
      </HeaderStyle.SocialLink>
    </HeaderStyle.MenuItem>
  </HeaderStyle.HeaderMenu>
);

const DesktopMenu = styled(Menu)`
  @media (max-width: 992px) {
    display: none;
  }
`;

const Header = () => {
  const [scrollObj, setScroll] = useState({});

  const handleScroll = () => {
    // Must use a new object, otherwise react will think it is the same
    const newScrollObj = { ...scrollObj };
    newScrollObj.scroll = window.scrollY;
    setScroll(newScrollObj);
  };
  useWindowEvent('scroll', handleScroll);

  useDidMount(() => {
    const el = document.querySelector('nav');
    setScroll({ top: el.offsetTop, height: el.offsetHeight, scroll: 0 });
  }, [handleScroll]);

  const isFixHeader = () => scrollObj.scroll > scrollObj.top;

  return (
    <HeaderStyle.FixedHeader fixed={isFixHeader()}>
      <HeaderStyle.HeaderGutter className="container">
        <HeaderStyle.NavBrand to="/">
          <span>Michael.</span>
        </HeaderStyle.NavBrand>

        <DesktopMenu />
      </HeaderStyle.HeaderGutter>
    </HeaderStyle.FixedHeader>
  );
};

export default Header;
