import React, { useRef, useEffect } from 'react';
import PropTypes from 'prop-types';
import styled from 'styled-components';
import AOS from 'aos';
import Bio from './sections/Bio';
import Intro from './sections/Intro';
import Skills from './sections/Skills';
import Contact from './sections/Contact';
import { Layout } from '../components/layout';
import AnimatedArrow from '../components/AnimatedArrow';
import IndexBg from '../images/index-bg.png';
import 'aos/dist/aos.css';

import { Meshline } from '../components/meshiline';

const HomeBannerSection = styled.section`
  width: 100%;
  position: relative;
  height: 100vh;
  z-index: 1;

  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
`;

const HomeBannerBio = styled(Bio)`
  position: relative;
  margin: 15px;
  padding: 15px;
  min-height: 350px;

  :after {
    content: '';
    position: absolute;
    top: -50px;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
    background-image: radial-gradient(
        ellipse closest-side,
        rgba(15, 14, 22, 0.75),
        var(--primary)
      ),
      url(${IndexBg});
    background-size: cover;
    background-repeat: no-repeat;
    opacity: 0.75;
  }
`;
const ScrollButton = styled.button`
  position: absolute;
  bottom: 25px;
  background: transparent;
  border: none;

  :focus {
    border: none;
    outline: none;
  }
`;

const Section = styled.section`
  margin-top: 144px;
  margin-bottom: 144px;
`;

const Triangle = styled.div`
  transform: translate3d(0px, 0px, 0px) rotate(45deg);
  position: absolute;
  width: 20px;
  height: 400px;
  border-radius: 12px;
  z-index: -1;
`;

const TealTriangle = styled(Triangle)`
  background: linear-gradient(to right, deeppink 0%, coral 100%);
  height: 400px;
`;

const PinkTriangle = styled(Triangle)`
  background: linear-gradient(to right, SlateBlue 0%, DeepSkyBlue 100%);
  margin-left: 45px;
  height: 400px;
`;
const TomatoTriangle = styled(Triangle)`
  background: linear-gradient(to right, tomato 0%, gold 100%);
  margin-left: 90px;
  height: 400px;
`;
const BackGround = styled(Meshline)`
  position: absolute;
  width: 100%;
  height: 500px;
`;

const scrollToRef = (ref) => window.scrollTo(0, ref.current.offsetTop - 100);

const Index = (props) => {
  const scrollRef = useRef(null);
  const executeScroll = () => {
    scrollToRef(scrollRef);
  };

  useEffect(() => {
    AOS.init({
      duration: 2000
    });
  });

  const { location } = props;
  return (
    <Layout location={location}>
      <HomeBannerSection>
        <BackGround />
        <HomeBannerBio />
        <ScrollButton onClick={executeScroll}>
          <AnimatedArrow />
        </ScrollButton>
      </HomeBannerSection>

      <Section ref={scrollRef} data-aos="fade-up">
        <div className="container">
          <Intro />
        </div>
      </Section>

      <Section data-aos="fade-up">
        <div className="container">
          <Skills />
        </div>
      </Section>

      <Section data-aos="fade-up">
        <div className="container">
          <Contact />
        </div>
      </Section>
    </Layout>
  );
};
Index.propTypes = {
  location: PropTypes.shape({
    hash: PropTypes.string.isRequired,
    key: PropTypes.string.isRequired,
    pathname: PropTypes.string.isRequired,
    search: PropTypes.string.isRequired
  }).isRequired
};
export default Index;
