import styled, { css } from 'styled-components';
import { Link } from 'gatsby';
import { Github, LinkedinIn } from '@styled-icons/fa-brands';
import { motion } from 'framer-motion';

const FixedHeader = styled.header`
  position: fixed;
  background-color: #100e12;
  color: #ffffff;
  box-sizing: border-box;
  padding: 20px 15px;
  top: 0;
  right: 0;
  left: 0;
  z-index: 1030;
  align-items: center;
  transition: top 0.3s;
  box-shadow: ${(props) => (props.fixed ? '0 4px 3px rgba(0,0,0,.5)' : '')};
`;

const HeaderInner = styled(motion.nav)`
  display: flex;
  position: relative;
  align-items: center;
`;
const HeaderGutter = styled(HeaderInner)`
  padding-left: 16px;
  padding-right: 16px;
`;

const NavBrand = styled(Link)`
  font-family: 'Sen', sans-serif;
  font-size: 1.8rem;
  font-weight: 600;

  flex-grow: 1;

  color: #fff;

  :hover {
    background: linear-gradient(to right, #f09819, #ff512f);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
  }

  @media (max-width: 768px) {
    font-size: 1.55rem;
  }
`;

const HeaderMenu = styled.ul`
  align-items: center;
  display: flex;
  font-size: 1rem;
  margin: 0;
  line-height: 1.5;
  box-sizing: border-box;
  outline: none;
  list-style: none;
  color: rgba(0, 0, 0, 0.65);
  line-height: 0;
  transition: background 0.3s, width 0.2s;

  @media all and (max-width: 600px) {
    line-height: 1;
    float: none;
    font-size: 13px;
  }
`;

const MenuItem = styled.li`
  display: inline-block;
  color: #ffffff;
  margin: 0;
`;

const LogoLinked = styled(Link)`
  color: #ffffff;
  text-decoration: none;
`;

const StyledLink = styled(LogoLinked)`
  font-family: 'Roboto', sans-serif;
  padding: 0.7em calc(0.7em * 1.2);
  position: relative;
  color: rgba(255, 255, 255, 0.5);

  :before {
    content: '';
    position: absolute;
    content: '';
    bottom: -3px;
    left: calc(0.7em * 1.2);
    right: calc(0.7em * 1.2);
    height: 3px;
    background: linear-gradient(to right, #f09819, #ff512f);
    transform: scaleX(0);
    transition: all 0.3s ease-in-out 0s;
  }

  :hover:before {
    visibility: visible;
    transform: scaleX(1);
  }

  :hover {
    background: linear-gradient(to right, #f09819, #ff512f);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
  }

  &.selected {
    background: linear-gradient(to right, #f09819, #ff512f);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    opacity: 1;
  }
  &.selected:before {
    visibility: visible;
    transform: scaleX(1);
  }
`;
const SocialLink = styled.a`
  padding: 0.7em calc(0.7em * 1.2);
  position: relative;

  color: inherit;
  transition: 200ms;
  transition-timing-function: ease-in-out;

  :hover {
    color: #f09819;
  }
`;
const StyledGithubIcon = styled(Github)`
  @media all and (max-width: 600px) {
    width: 22px;
  }
`;

const StyledLinkedinInIcon = styled(LinkedinIn)`
  @media all and (max-width: 600px) {
    width: 22px;
  }
`;

export default {
  FixedHeader,
  HeaderInner,
  HeaderGutter,
  NavBrand,
  HeaderMenu,
  MenuItem,
  SocialLink,
  StyledGithubIcon,
  StyledLink,
  StyledLinkedinInIcon
};
